import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";

export default function Activities() {
  const { subjectTitle } = useParams();
  const [acts, setActs] = useState([]);

  useEffect(() => {
    const contentful = require("contentful");

    const client = contentful.createClient({
      space: process.env.REACT_APP_CONTENTFUL_SPACE,
      environment: process.env.REACT_APP_CONTENTFUL_ENVIRONMENT,
      accessToken: process.env.REACT_APP_CONTENTFUL_TOKEN,
    });

    client
      .getEntries({
        content_type: "activity",
        "fields.subject": subjectTitle,
      }) // Specify content type 'project'
      .then((entries) => {
        setActs(entries.items); // Set state with all project entries
      })
      .catch(console.error);
  }, [subjectTitle]);

  console.log(acts);

  return (
    <div className="pt-32">
      {acts.map((act, index) => (
        <a href={"/portfolio-archi/#/activity/" + act.sys.id} key={index}>
          <h1>{act.fields.title}</h1>
          {/* <img src={activity.fields?.preview.fields.file.url} alt="test" /> */}
        </a>
      ))}
    </div>
  );
}
